// 공통 레이아웃
.hide, .blind, caption, legend { opacity:0; text-indent:-999em; overflow: hidden; position: absolute; top: 0; left: 0; width: 0; height: 0; font-size: 0; line-height: 0; }
html, body {
    overflow: hidden;
    margin: 0;
    width: 100%;
    height: 100%;
    background-color: #f1f3f4;
}
.box-white {
    display: block;
    color:#666;
    background-color: #fff;
    border-radius: 0.5rem;
    border:none;
    overflow: hidden;
    box-shadow:0 2px 3px rgba(0,0,0,0.1);
    padding:1.5rem;
    font-size:1.2rem;
    font-weight: 400;
}
.box-gray {
    display: block;
    color:#fff;
    background-color: #878d90;
    border:none;
    overflow: hidden;
    padding:2rem 1.5rem;
    font-size:1.4rem;
    font-weight: 500;
    .button.is-transparent {
        background-color: transparent;
        border:none;
        color:#fff;
    }
    .box-device-q {
        margin-bottom: 0.5rem;
        display: block;
    }
    .box-device-q + .field {
        .control:first-child {
            width: 60%;
        }
    }
}
.bg-white {
    background-color: #fff;
}
// content
#content {
    overflow: auto;
    height: 100%;
}
#content::before {
    position: fixed;
    overflow: hidden;
	content: '';
	top: 0;
	left: 0;
	z-index: 100;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.7);
	opacity: 0;
	-webkit-transition: opacity 0.3s, -webkit-transform 0s 0.3s;
	transition: opacity 0.3s, transform 0s 0.3s;
	-webkit-transform: translate3d(100%,0,0);
	transform: translate3d(100%,0,0);
}

// header
.header {
    .navbar {
        color:#333;
        min-height: 5rem;
        position: fixed;
        width: 100%;
        z-index: 29;
        box-shadow: 0 1px 10px rgba(0,0,0,0.1);
        .navbar-brand, .navbar-tabs {
            min-height: 5rem;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            
        }
        .navbar-burger {
            margin-left: initial;
            width:4.4rem;
            height: 5rem;
            background-color: transparent;
            border:none;
            outline:none;
            float: left;
        }
        .header-title {
            font-size: 2rem;
            font-weight: 600;
            color: #333;
        }
        .header-first {
            position: absolute;
            top:0;
            left:0;
            .navbar-prev {
                i:before{
                    content: '\e94a';
                }
            }
        }
        .header-menus {
            position: absolute;
            top:0;
            right:0;
            padding-right: 1rem;
            .icon-topnav {
                display: flex;
                float: left;
                padding:0 0.6rem;
                height: 5rem;
                border:none;
                overflow: hidden;
                outline: 0;
                align-items: center;
                i {
                    display: inline-block;
                    width:2rem;
                    height: 2rem;
                    margin-top:-2px;
                    background-image: url(../images/icon/icon_topnav.png);
                    background-repeat: no-repeat;
                    background-size: 1.9rem auto;
                }
                &.i-msg i {
                    background-position: 0 1px;
                }
                &.i-device i {
                    width:1.4rem;
                    background-position: 0 -27px;
                }
                &.i-my i {
                    width:1.6rem;
                    background-position: 0 -56px;
                }
            }
            .navbar-close {
                margin-right: -1rem;
                i:before{
                    content: '\e946';
                }
            }
        }
    }
}

.button-topnav {
    color:#333;
    border:none;
    outline:none;
    min-height: 5rem;
    margin:0;
    padding:0;
    width:4.4rem;
    height: 5rem;
    background-color: transparent;
    i:before{
        font-size: 1.8rem;
        font-weight: bold;
        display: inline-block;
    }
    &:active {
        background-color: rgba(0,0,0,0.05);
    }
}

/* Shown menu */
.__show-menu {
    #content {
        overflow: hidden;
    }
}
.__show-menu {
    position: fixed;
    .menu-wrap {
        transform: translate3d(0,0,0);
    }
    #content::before {
        opacity: 1;
        transition: opacity 0.3s;
        transform: translate3d(0,0,0);
    }
}

// #main
