#sub {
    &.navbar-blue {
        .navbar {
            background-color: #0ac4d1;
            box-shadow: none;
            .header-title, .navbar-burger, .button-topnav {
                color:#fff;
            }
            .header-menus .icon-topnav i {
                background-image: url(../images/icon/icon_topnav_w.png);
            }
        }
