}
// 메인 뉴스티커용
.tickerwrap {
    position: absolute;
    top: 0;
    left:0;
    border: 0;
    background: transparent;
    width: 100%;
    height: 3rem;
    line-height: 3rem;
    overflow: hidden;
    cursor: pointer;
    ul.ticker-list {
        position: relative;
        display: inline-block;
        list-style: none;
        padding:0;
        margin:0;
        &.cloned {
            position: absolute;
            top: 0px;
            left: 0px;
        }
        li {
            float: left;
            padding-left: 20px;
            a {
                color: #9effec;
                font-weight: 500;   
            }
        }
        &.cloned {
            li {
                opacity:1;
            }
        }
    }
}
.ticker {
    position: relative;
    background-image: url(../images/main/bg_ticker.gif);
    background-size: 3px auto;
    color:#9effec;
    overflow: hidden;
    font-weight: 500;
    font-size: 1.3rem;
    padding:0.7rem 0;
    line-height: 1;
    border-radius:0.5rem;
    height: 2.7rem;
    margin:0 1.5rem;
}

.top-section {
    &.sub-main {
        
        .field {
            .radio {
                margin-right: 0;
            }
            .radio + .radio {
                margin-left: 0.1rem;
            }
        }
        .radio-box-indi {
            border-bottom:1px solid #d2d8db;
            margin-bottom: 1rem;
            padding-bottom: 1.5rem;
        }

    }
    &-title {
        color:#fff;
        padding:0 0 1.3rem;
        font-weight: 400;
        font-size: 1.3rem;
        line-height: 1.1;
        strong {
            display: block;
            font-weight: 400;
            font-size: 3rem;
            margin-top: 0.5rem;
            color: #fff;
        }
        .flex-left {
            flex-grow: 1;
        }
        .flex-right {
            flex-grow: 0;
        }
    }
    .switch-radio {
        margin:1.6rem auto 1rem;
    }
    .section {
        padding:0;
        visibility: hidden;
        opacity:0;
        position: absolute;
        transition: opacity 0.5s ease-in-out;
        &.is-active {
            visibility: visible;
            opacity:1;
            position: relative;
        }
    }
    .card {
        display: block;
        padding:1.25rem 1.5rem;
        &.box-indi {
            display: none;
            &.is-active {
                display: block;
            }
            .card-content {
                .field.is-center {
                    text-align: center;
                    margin:1rem 0;
                }
            }
        }
        .card-header {
            display: flex;
            margin-bottom: 0.8rem;
            .state {
                vertical-align: middle;
                font-size: 1.1rem;
                font-weight: 400;
                color:#777;
                display: inline-block;
                font-weight: 400;
                padding: 0.4rem 0.5rem 0.2rem;
                line-height: 1;
                border:1px solid #999;
                border-radius: 0.5rem;
                margin-top: -0.3rem;
            }
        }
        .card-vs {
            display: flex;
            li {
                flex-grow: 1;
                width:50%;
                border-left:1px solid #e9edee;
                padding-left:3rem;
                padding-bottom:1rem;
                &:first-child {
                    border:none;
                    padding-left:0;
                    padding-right:3rem;
                }
                strong.number {
                    font-weight: 600;
                    font-size: 1.7rem;
                }
                .unit {
                    font-size: 1rem;
                    font-weight: 400;
                }
                .select {
                    width:100%;
                    justify-content: left;
                    font-size: 1.2rem;
                    font-weight: 400;
                    border:none;
                    border-radius:5px;
                    cursor: pointer;
                    background-color: whitesmoke;
                    color: rgba(0, 0, 0, 0.7);
                    padding: 0.8em 0.5em;
                    margin-bottom: 0.6rem;
                    white-space: nowrap;
                    height:auto;
                }
                .vs-content {
                    p {
                        margin:0;
                        padding:0;
                        line-height: 1.1;
                    }
                    .no-data {
                        min-height: 4rem;
                        display: flex;
                        align-items: center;
                        color: #bbb;
                        font-size: 1rem;
                        font-weight: 400;
                    }
                }
            }
            &:after {
                position: absolute;
                content:'';
                top:1.0rem;
                left:50%;
                margin-left: -1.7rem;
                width:3.4rem;
                height:5.0rem;
                background: url(../images/icon/vs.png) 0 0 no-repeat;
                background-size: 3.4rem auto;
            }
            .btn-select {
                // color:#bbb;
                font-weight: 400;
            }
        }
        .vs-bg {
            display: block;
            border-top:1px solid #e9edee;
            margin:0 -1.5rem;
            line-height: 1.3;
            padding:1.5rem 1.5rem 0.2rem;
            font-size: 1.5rem;
            color:#555;
            font-weight: 400;
            background: url(../images/icon/icon_graph.png) 95% 95% no-repeat;
            background-size: 5rem auto;
            letter-spacing: -1px;
            strong, .down {
                color:#04b1c6;
                font-weight: 600;
                letter-spacing: -1.5px;
            }
            .up {
                color:#ff6360;
                font-weight: 600;
            }
        }
        .card-content {
            padding:0;
            .btn-guide {
                padding:0.5rem 0;
                border:0;
                float: right;
                font-size: 1.1rem;
                font-weight: 400;
                height:auto;
                color:#aaa;
                outline:none;
                .i-info {
                    margin-left: 0.2rem;
                    vertical-align: middle;
                    margin-top: -2px;
                    &:before {
                        font-size: 1.2rem;
                    }
                }
            }
        }
        .card-content-footer {
            margin-top:1.8rem;
            position: relative;
        }
    }
}
.sub-main .is-center {
    text-align: center;
}
#box-memo {
    display: none;
    .btns-memo {
        position: absolute;
        top:-0.8rem;
        right:0;
        text-align: right;
    }
    textarea {
        font-size:1.4rem;
        padding: 1rem;
    }
}

// 식사 > 음식 추가
.section-search {
    text-align: center;
    color:#aaa;
    font-size: 1.2rem;
    padding:2rem 1.5rem;
    .search-field {
        position: relative;
        margin-bottom: 2rem;
        &.is-auto-complete {
            .input {
                border-bottom-left-radius: 0;
                border-bottom-right-radius: 0;
            }
            .auto-complete-container {
                visibility: visible;
                opacity: 1;
            }
        }
        .input {
            border-color:#888;
            color:#04b1c6;
            font-weight: 500;
            padding-right:3rem
        }
        .icon-search {
            position: absolute;
            top:50%;
            margin-top: -0.8rem;
            right:0.1rem;
        }
        .auto-complete-container {
            text-align:left;
            position: absolute;
            overflow-x: hidden;
            overflow-y: auto;
            max-height:12rem;
            top:4.9rem;
            left:0; 
            right:0;
            background-color: #fff;
            border:1px solid #888;
            border-top:1px solid #e5e5e5;
            border-bottom-left-radius:5px;
            border-bottom-right-radius:5px;
            box-shadow: 0 4px 3px rgba(0,0,0,0.1);
            visibility: hidden;
            opacity: 0;
            transition: all 0.2s ease-in-out;
            li {
                border-bottom:1px solid #e5e5e5;
                padding:0.8rem 1rem;
            }
        }
    }
    .search-desc {
        margin-top: 1rem;
    }
    .flex.is-grouped.is-full {
        margin-top: 2rem;
    }
}
.section-add-list {
    padding-top: 2rem;
    .sub-delete-list {
        .dot-list li .meal-item {
            display: block;
            min-width: 100%;
            margin-right: 0;
            strong {
                display: block;
                word-wrap:normal;
                word-break: break-all;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }
        }
        .flex1 {
            overflow: hidden;
        }
        .photo {
            img {
                max-width: 7rem;
                max-height: 5rem;
            }
        }
        .flex0 {
            display: flex;
            justify-content: flex-end;
            min-width:21.5rem;
            font-size: 1.2rem;
            display: flex;
            align-items: center;
        }
        .step-wrap {
            display: inline-block;
            vertical-align: middle;
            padding:0 0.5rem 0 1rem;
            .button {
                padding:0;
                font-size: 1rem;
                width: 2.8rem;
                height: 2.8rem;
                vertical-align: middle;
            }
            span {
                display: inline-block;
                vertical-align: middle;
            }
            em {
                display: inline-block;
                vertical-align: middle;
                font-style: normal;
            }
            .center {
                width:4rem;
                letter-spacing: -1px;
                text-align: center;
            }
        }
    }
}
// 음식추가 > 자주먹는음식 팝업
