// 복약 상세 메인
.section-list-drug {
    padding-top: 0;
    margin-top: -1.5rem;
}
.card-drug .field .control {
    justify-content:space-between;
    width:calc(50% - 0.375rem);
}
.card-drug .field .button {
    font-size: 1.3rem;
}
.card-drug-title, .card-drug-desc {
    text-align: center;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.4;
    color:#111;
    padding-bottom:1.5rem;
}
.card-drug-title {
    font-size: 1.6rem;
    border-bottom:1px dashed #dcdcdc;
    strong {
        font-weight: 500;
    }
}
.card-drug-desc {
    padding-top:1.5rem;
    font-size: 1.4rem;
    font-weight: 500;
    color:#51c2cf;
    span {
        display: inline-block;
    }
}
.list-drug {
    li {
        font-size: 1.3rem;
        padding:1.5rem 0;
        border-bottom: 1px solid #dcdcdc;
    }
    .date-date {
        color:#111;
        font-weight: 500;
        display: inline-block;
    }
    .date-time {
        margin-left: 0.5rem;
        color:#ff6464;
        font-weight: 500;
        display: inline-block;
    }
    .drug-stat {
        width: 5rem;
        height: 5rem;
        display: block;
        border: 1px solid #dcdcdc;
        border-radius:5rem;
        position: relative;
        &:before {
            position: absolute;
            content:'';
            width: 1.5rem;
            height: 1.5rem;
            top:50%;
            left:50%;
            margin:-0.75rem 0 0 -0.75rem;
            display: block;
        }
        &.stat-true:before {
            background: url(../images/icon/icon_check.png) 50% 50% no-repeat;
            background-size: 1.5rem auto;
        }
        &.stat-false:before {
            background: url(../images/icon/icon_cross.png) 50% 50% no-repeat;
            background-size: 1.5rem auto;
        }
    }
    .flex1.is-right {
        padding-left: 1rem;
        display: flex;
        justify-content: flex-end;
