// Graph
.graphs {
    position: relative;
    z-index: 1;
    .pointer {
        margin:0;
        position: absolute;
        background-color: #fd4a61;
        transition: left 1s ease-in-out;
        text-indent: -999em;
        display: block;
        overflow: hidden;
        top:1.1rem;
        left:0;
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 1rem;
        border:2px solid #fff;
        box-shadow: 0px 2px 5px rgba(0,0,0,0.2);
    }
    .pointer-right {
        margin:0;
        position: absolute;
        background-color: #555;
        transition: left 1s ease-in-out;
        text-indent: -999em;
        display: block;
        overflow: hidden;
        top:1.1rem;
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 1rem;
        border:2px solid #fff;
        box-shadow: 0px 2px 5px rgba(0,0,0,0.2);
        &:after {
            content:'\eac9';
            display: block;
            position: absolute;
            color:#fff;
            top:0;
            font-family: 'iconset'!important;
            font-size: 0.7rem;
            text-indent: 0;
            width: 1.3rem;
            height: 1.3rem;
            text-align: center;
            speak: none;
            font-style: normal;
            font-weight: 400;
            font-variant: normal;
            text-transform: none;
            line-height: 1.2rem;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        } 
    }
    .bg {
        padding:14px 0 0 0;
        -moz-appearance: none;
        -webkit-appearance: none;
        border: none;
        display: block;
        overflow: hidden;
        width: 100%;
        .state {
            position: relative;
            z-index: -1;
            display: block;
            float: left;
            min-height: 1rem;
            &:first-child {
                border-top-left-radius : 2rem;
                border-bottom-left-radius : 2rem;
            }
            &:last-child {
                border-top-right-radius : 2rem;
                border-bottom-right-radius : 2rem;
            }
            &-blue {
                background-color: #4ac2d0;
            }
            &-sky {
                background-color: #80dce6;
            }
            &-gray {
                background-color: #d9dddf;
            }
            &-yellow {
                background-color: #fccd3f;
            }
            &-red {
                background-color: #ff6360;
            }
            em {
                position: absolute;
                right:0;
                top:-13px;
                font-size: 0.9rem;
                color: #aaa;
                font-style: normal;
            }
        }
    }
    &.graphs-survey {
        padding-top:1.6rem;
        margin-bottom: 2rem;
        .graph-header-start,.graph-header-end {
            position: absolute;
            font-size: 1rem;
            color:#888;
            top:0;
        }
        .graph-header-start {
            left:0;
        }
        .graph-header-end {
            right:0;
        }
        .bg {
            background-color: #ddd;
            padding:0;
            overflow: hidden;
            border-radius:2rem;
            z-index: 0;
        }
        .state {
            z-index: 1;
            border-radius:0 !important;
        }
    }
