    }
}
.faq-list {
    color:#555;
    margin:0 -1.5rem;
    font-size: 1.3rem;
    dl:first-child {
        .accordion-head {
            a {
                border-top: none;
            }
        }
    }
    .accordion-item {
        position: relative;
        &:after {
            content:'';
            display: block;
            margin:0 1.5rem;
            border-bottom: 1px solid #dcdcdc;
            position: absolute;
            bottom:-1px;
            height: 1px;
            left:0;
            right:0;
            z-index: 0;
        }
    }
    .accordion-head {
        position: relative;
        z-index: 1;
        background-color: transparent;
        padding:0 1.5rem;
        transition: all 0.1s ease-in-out;
        a {
            position: relative;
            color:#555;
            display: block;
            line-height: 1;
            .board-list-title {
                color:#555;
                padding:1.4rem 0;
                font-weight: 500;
                margin: 0;
            }
            &:after {
                position: absolute;
                color:#bbb;
                font-family: 'iconset'!important;
                speak: none;
                font-style: normal;
                font-weight: 600;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                content: '\e94b';
                display: inline-block;
                height: 12px;
                width: 12px;
                top:50%;
                right:0;
                margin:-6px 0 0 0;
                transform: rotate(0);
                transition: transform 0.3s ease-in-out;
            }
        }
    }
    .accordion-panel {
        padding:1.5rem;
        font-weight: 400;
        font-size: 1.3rem;
        display: none;
    }
    .is-active {
        .accordion-head {
            background-color: rgba(0,0,0,0.4);
            a {
                border:none;
                strong {
                    color:#fff;
                }
                &:after {
                    color:#fff;
                    transform: rotate(180deg);
                }
            }
        }
    }
}
